.page {
  background-color: white;
  padding: 0;
  margin: 0 auto;
  max-width: 1024px;
}

.header {
  max-width: 1024px;
  padding: 0;
  margin: 10px auto;
  box-sizing: border-box;
  font-family: "IBM Plex Serif", serif, Arial;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.2;
  background-color: #ffff;
}

@media screen and (max-width: 450px) {
  .header {
    justify-content: center;
  }
}

.header__logo {
  height: 100px;
  background-image: url(/rom-redux/static/media/logo.89a7afd9.png);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  margin: 0 auto;
}

@media screen and (max-width: 650px){
  .header__logo {
    height: 70px;
  }
}

@media screen and (max-width: 450px){
  .header__logo {
    height: 50px;
  }
}
.navBar {
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  font-family: "IBM Plex Serif", serif, Arial;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.2;
}

@media screen and (max-width: 400px) {
  .navBar  {
    justify-content: center;
    padding-top: 10px;
  }
}

@media screen and (max-width: 325px) {
  .navBar  {
    font-size: 12px;
  }
}

.main {
  height: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  max-width: 1024px;
  font-family: "IBM Plex Serif", serif, Arial;
}

@media screen and (max-width: 550px) {
  .main {
    padding-top: 10px;
  }
}

.main__option {
  display: flex;
  flex-direction: column;
  max-width: 325px;
  min-height: 200px;
  margin: 150px auto;
  position: relative;
}

@media screen and (max-width: 350px) {
  .main__option {
    flex-direction: column;
  }
}

.main__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 20px 0 0 0;
}

.main__count {
  font-family: 'IBM Plex Serif', serif, Arial;
  font-weight: 600;
  font-size: 140px;
  text-align: center;
}

.main__input {
  width: 200px;
  height: 25px;
  margin: 0;
  padding-left: 5px;
  border-radius: 5px;
  border: 1px solid gray;
  box-sizing: border-box;
}

@media screen and (max-width: 350px) {
  .main__input {
    margin: 0;
  }
}

.main__text {
  font-family: "IBM Plex Serif", serif, Arial;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.4;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 450px) {
  .main__text {
    font-size: 12px;
  }
}

.main__title {
  align-self: center;
  font-family: 'IBM Plex Serif', serif, Arial;
  font-weight: 600;
  font-size: 26px;
  line-height: 1.46;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 450px) {
  .main__title {
    font-size: 20px;
  }
}

.main_counter {
  margin: 0 auto;
  padding: 0;
}
.main__label {
  padding: 0;
  margin: 0;
}

.book {
  margin: auto;
  max-width: 1024px;
}

@media screen and (max-width: 550px){
  .book {
    padding-top: 10px;
  }
}

.book__textArea {
  border: none;
  width: 100%;
  min-height: 60vh;
  line-height: 1.8;
  overflow: overlay;
  resize: none;
  padding: 0px 20px;
  margin: 0;
  box-sizing: border-box;
  max-width: 1024px;
  font-size: 20px;
}

@media screen and (max-width: 1024px) {
  .book__textArea {
    font-size: 18px;
  }
}

@media screen and (max-width: 768px) {
  .book__textArea {
    font-size: 16px;
  }
}

.timer {
  margin: 0 20px;
  padding: 0;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 400px) {
  .timer {
    align-items: center;
    justify-content: center;
  }
}

.timer__area {
  display: flex;
  max-width: 300px;
}

.timer__block {
  font-family: 'IBM Plex Serif', serif, Arial;
  font-weight: 600;
  font-size: 15px;
  line-height: 1.4;
  margin: 0;
}

.popup {
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.3s linear;
  -webkit-transition: visibility 0.3s, opacity 0.3s linear;
  -moz-transition: visibility 0.3s, opacity 0.3s linear;
  -ms-transition: visibility 0.3s, opacity 0.3s linear;
  -o-transition: visibility 0.3s, opacity 0.3s linear;
  z-index: 999;
}

.popup__close-button {
  width: 32px;
  height: 32px;
  background-image: url(/rom-redux/static/media/close-button.f61a2bae.svg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  box-sizing: border-box;
  transition: 0.3s;
  padding: 0;
  margin: 0;
  border: none;
  position: absolute;
  right: -40px;
  top: -40px;
}

.popup__close-button:hover {
  cursor: pointer;
  opacity: 0.6;
}

@media screen and (max-width: 520px) {
  .popup__close-button {
    width: 20px;
    height: 20px;
    right: 0;
  }
}
.popup__container {
  display: flex;
  align-self: center;
  position: relative;
  margin: 0 auto;
  min-height: 540px;
}

@media screen and (max-width: 450px){
  .popup__container {
    min-height: 380px;
    width: 350px;
  }

}
@media screen and (max-width: 390px){
  .popup__container {
    min-height: 380px;
    width: 282px;
  }
}

.popup__title {
  max-width: 330px;
  font-size: 24px;
  font-weight: 900;
  font-style: normal;
  line-height: 1.22;
  color: #000000;
  padding-top: 35px;
  margin: 0;
}

@media screen and (max-width: 520px) {
  .popup__title {
    font-size: 18px;
    padding-top: 27px;
  }
}
.popup__window {
  width: 430px;
  border-radius: 10px;
  background-color: #ffffff;
  margin: 0;
  padding: 20px 20px 20px;
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgb(0 0 0 / 15%);
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 520px) {
  .popup__window {
    min-width: 100%;
  }
}

.popup__image {
  height: 50%;
  background-image: url(/rom-redux/static/media/alarm.8855dfae.png);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.popup_opened {
  display: flex;
  opacity: 1;
  visibility: visible;
}
.footer {
  min-height: 40px;
  max-width: 1024px;
  padding: 20px;
  margin: 0 auto;
  box-sizing: border-box;
  border-top: 1px solid #2196f3;
  font-family: "IBM Plex Serif", serif, Arial;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.2;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #ffff;
}

@media screen and (max-width: 370px) {
  .footer {
    display: flex;
    flex-direction: column;
    font-size: 12px;
  }
}

.footer__caption {
  font-family: 'IBM Plex Serif', serif, Arial;
  font-weight: 600;
  font-size: 15px;
  line-height: 1.2;
}

@media screen and (max-width: 370px){
  .footer__caption {
    font-size: 12px;
  }
}

.button {
  padding: 5px;
  margin: 0;
  height: 25px;
  min-width: -webkit-max-content;
  min-width: max-content;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  font-family: "IBM Plex Serif", serif, Arial;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  color: #fff;
  text-decoration: none;
  border: 0;
  border-radius: 5px;
  box-sizing: border-box;
  box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
  background-color: #2196f3;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.button:hover {
  opacity: 0.8;
  -webkit-text-decoration: solid;
          text-decoration: solid;
}

@media screen and (max-width: 325px) {
  .button {
    font-size: 12px;
    text-align: center;
  }
}

.button__main {
  align-self: end;
  margin: 20px 0 0 0;
}

@media screen and (max-width: 400px) {
  .button__main {
    align-self: normal;
  }
}

.button__navBar {
  margin: 0 10px 0 0;
}

.button__about {
  margin-top: 45px;
}

.button__guide {
  margin: 45px 0 0 15px;
  width: 120px;
}

.button_idle {
  border: 0;
  color: #fff;
  background-color: #2196f375;
}

.button_active {
  border: 0;
  color: #fff;
  background-color: #f50057;
}

.about {
  max-width: 500px;
  height: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  font-family: "IBM Plex Serif", serif, Arial;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 10px;
}

.about__title {
  font-weight: 600;
  font-size: 26px;
  line-height: 1.46;
  margin: 0 auto;
  padding: 0;
  text-align: center;
}

@media screen and (max-width: 450px) {
  .about__title {
    font-size: 20px;
  }
}

.about__text {
  font-family: "IBM Plex Serif", serif, Arial;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.4;
  margin: 0;
  padding: 15px 0 0 0;
}

@media screen and (max-width: 450px) {
  .about__text {
    font-size: 12px;
  }
}

.guide {
  max-width: 500px;
  height: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  font-family: "IBM Plex Serif", serif, Arial;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 10px;
}

.guide__list {
  list-style-type: decimal;
  margin: 0;
}

@media screen and (max-width:450px){
  .guide__list {
    padding: 0 10px;
  }
}

.guide__actions {
  display: flex;
  flex-direction: row;
}
.results {
  max-width: 500px;
  height: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  font-family: "IBM Plex Serif", serif, Arial;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.results__title {
  font-weight: 600;
  font-size: 26px;
  line-height: 1.46;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 450px) {
  .results__title {
    font-size: 20px;
  }
}

.results__text {
  font-family: "IBM Plex Serif", serif, Arial;
  font-weight: 400;
  font-size: 36px;
  line-height: 1.4;
  margin: 0;
  padding: 0;
}

.results__text:nth-of-type(2) {
  border-right: 1px solid grey;
  border-left: 1px solid grey;

}
.results__text:nth-of-type(3) {
  text-align: end;
  width: 40%;
}

@media screen and (max-width: 450px) {
  .results__text {
    font-size: 12px;
  }
}

